import React from 'react';
import styles from './Settings.module.css';

const Settings = (props) => {
  return (
    <div className="p-5">
      <h5 className="text-center">   -:</h5>
      <br />
      <div className="row">
        <div className="col-md-6">
          <form>
            {/* <!--input type="text" value="password" name="type" hidden--> */}
            <h6>     ALPHASMS</h6>
            <div className={`form-row ${styles.formRowCustomStyle}`}>

              <div className="form-group col-lg-6 col-md-12 col-sm-6">
                <label htmlFor="input_sms_login"></label>
                <input type="text" value={props.smsLogin} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder=" AlphaSMS" name="smsLogin" required />
              </div>

              <div className="form-group col-lg-6 col-md-12 col-sm-6">
                <label htmlFor="input_sms_pass"></label>
                <input type="password" value={props.smsPass} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder=" AlphaSMS" name="smsPass" required />
              </div>

              <div className="form-group col-md-12">
                <label htmlFor="input_sms_api_key">API </label>
                <input type="text" value={props.smsApiKey} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder="API  AlphaSMS" name="smsApiKey" required />
              </div>
              <div className="form-group col-md-12">
                <label htmlFor="input_sms_alpha_name">ALPHA '</label>
                <input type="text" value={props.smsAlphaName} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" id="smsalphaname" placeholder="Alpha-'" name="smsAlphaName" required />
              </div>
              <div className="form-group col-md-12">
                <label htmlFor="input_sms_text"> </label>
                <textarea className="form-control" value={props.smsTextTemplate} onChange={(e) => props.updateState(e.target.name, e.target.value)} rows="4" placeholder="  " name="smsTextTemplate" required></textarea>
                <span id="counter">0  - 0 </span>
              </div>

              <div className="form-group col-md-12">
                <div className={`row ${styles.rowCustomStyle}`}></div>
              </div>
            </div>
          </form>
        </div>
        <div className="col-md-6 text-justify">
          <p><b>ASMS</b> -      SMS-     .       ,              ,   .</p>
          <p> SMS     9:00  20:00,       11:00  18:00.   .</p>
          <p><b>!</b>  -      .       .       </p>
          <p>    -    .</p>
          <p>        SMS.  - <b>70</b>    SMS.</p>
        </div>
      </div>

      <div className="row text-center">
        <div className="col-3"></div>
        <div className="col-sm-3">
          <div className="btn btn-outline-success btn-block" onClick={() => props.changeSettingsSmsHandler()}> </div>
        </div>

        <div className="col-sm-3">
          <div className="btn btn-outline-danger btn-block"> </div>
        </div>
        <div className="col-sm-3"></div>
      </div>
    </div>
  );
}

export default Settings;
