import React from 'react';
import styles from './ChangePass.module.css';

const ChangePass = (props) => {
  return (
    <>
      <h6 className={styles.titlePassCustomStyle}> </h6>

      <div className="form-group col-md-12">
        <label htmlFor="inputNewPassword"> </label>
        <input type="password" className="form-control" value={props.newPassword} onChange={(e) => props.updateState(e.target.name, e.target.value)} id="inputNewPassword" placeholder=" " name="newPassword" required />
      </div>
      <div className="form-group col-md-12">
        <label htmlFor="confirmNewPassword">   </label>
        <input type="password" className="form-control" value={props.repeatPassword} onChange={(e) => props.updateState(e.target.name, e.target.value)} id="confirmNewPassword" placeholder="   " name="repeatPassword" required />
      </div>
      <div className="mt-4">
        <div className={`row ${styles.customStyleForRow}`}></div>
        <div className="btn btn-outline-success btn-block" onClick={() => props.changePassHandler()}> </div>
      </div>
    </>
  );
}

export default ChangePass;
