import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import mainLogo from '../../assets/images/mainLogo.png';
import './Login.css';

const Login = (props) => {
  return (
    <>
      <div className="customDivStyles16"></div>
      <div className="container w-50">
        <div className="col-md-12 col-sm-8 border border-success rounded-lg">
          <div className="row customDivStyles1"></div>
          <div className="text-center">
            <img src={mainLogo} width="40%" alt="OTIS - -      " title="OTIS - -      " />
            <span className="customFontSizeSpan">OFFICE</span>
          </div>
          <div className="row customDivStyles1"></div>
          <h3 className="text-center"></h3>


          <div className="form-group">
            <label htmlFor="exampleInputEmail1" className="font-weight-bolder"></label>
            <input name="email" type="email" value={props.inputEmail} onChange={(e) => props.updateEmail(e.target.value)} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="  " />
            <small id="emailHelp" className="form-text text-muted">    E-mail </small>
          </div>

          <div className="form-group">
            <label htmlFor="exampleInputPassword1" className="font-weight-bolder"></label>
            <input name="password" type="password" value={props.inputPass} onChange={(e) => props.updatePass(e.target.value)} className="form-control" id="exampleInputPassword1" placeholder=" " />
            <small id="passHelp" className="form-text text-muted">    !</small>
          </div>
          <div className="form-group form-check">
            <input type="checkbox" className="form-check-input" id="exampleCheck1" />
            <label className="form-check-label" htmlFor="exampleCheck1">' </label>
          </div>
          <div className="text-center">
            <button onClick={() => props.login(props.inputEmail, props.inputPass)} className="btn btn-outline-success btn-block">  </button>
            {/* <button type="submit" className="btn btn-outline-success btn-block">  </button> */}
          </div>


          <div className="customDivStyles16"></div>
          <div className="row text-center">
            <div className="col-lg-6 col-md-12">
              <a href="#s" className="badge badge-light"> ' </a>
            </div>
          </div>
          <div className="row customDivStyles16"></div>
          <div className="text-bottom text-center">
            <small>&copy; All right reserved, <a href="authors.html">OTIS 2020</a></small>
          </div>

          <div className="row customDivStyles1"></div>
        </div>
      </div>
    </>
  );
}

export default Login;
