import React from 'react';
import styles from './PersonalData.module.css';

const PersonalData = (props) => {
  return (
    <>
      <h6 className={styles.titleCustomStyle}>   </h6>
      <div className="form-group col-md-12">
        <label htmlFor="inputNameDep">  / </label>
        <input type="text" value={props.firmName} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder="  / " id='inputNameDep' name="firmName" title="     -  " />
      </div>

      <div className="form-group col-md-12">
        <label htmlFor="inputaddress"></label>
        <input type="text" value={props.street} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder=". , . , . 16, . 4" id='inputaddress' name="street" title="     -  " />
      </div>

      <div className="form-group col-md-12">
        <label htmlFor="inputWeb">WEB-</label>
        <input type="text" value={props.webSite} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder="www.yourfirm.com.ua" id='inputWeb' name="webSite" title="     -  " />
      </div>

      <div className="form-group col-md-6">
        <label htmlFor="inputSurname"> </label>
        <input type="text" value={props.secondName} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" id="inputSurname" placeholder="" name="secondName" />
      </div>
      <div className="form-group col-md-6">
        <label htmlFor="inputName">' </label>
        <input type="text" value={props.firstName} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" id="inputName" placeholder="'" name="firstName" />
      </div>
      <div className="form-group col-md-6">
        <label htmlFor="inputBirthday">  </label>
        <input type="date" value={props.dateBirthday} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" id="inputBirthday" name="dateBirthday" />
      </div>
      <div className="form-group col-md-6">
        <label htmlFor="inputPhone">  </label>
        <input type="text" value={props.telephoneNumber} onChange={(e) => props.updateState(e.target.name, e.target.value)} className="form-control" placeholder="  " id='inputPhone' name="telephoneNumber" title="     -  " />
      </div>
      <div className="mt-4">
        <div className={`row ${styles.customStyleForRow}`}></div>
        <div className="btn btn-outline-success btn-block" onClick={() => props.changeUserDataHandler()}> </div>
      </div>
    </>
  );
}

export default PersonalData;
