import React from 'react';
import TablePart from './TablePart/TablePart';

const Main = (props) => {
  return (
    <div className="container pt-2">


      <table className="table table-hover col-lg-8 col-md-12">
        <tbody className="user-info">

          <TablePart title='' colorClass='secondary' data={props.userName} />
          <TablePart title=' ' colorClass='secondary' data={props.dateOfReg} />
          <TablePart title='  / ' colorClass='secondary' data={props.firmName} />
          <TablePart title='' colorClass='secondary' data={props.street} />
          <TablePart title=' ' colorClass='secondary' data={props.telephoneNum} />
          <TablePart title='E-mail' colorClass='secondary' data={props.email} />
          <TablePart title='  ' colorClass='info' data={props.firmsCount} />
          <TablePart title='   ' colorClass='info' data={props.carsCount} />
          <TablePart title='   30 ' colorClass='warning' data={props.sumThirtyTO} />
          <TablePart title='   14 ' colorClass='warning' data={props.sumFourteenTO} />
          <TablePart title='   30 ' colorClass='warning' data={props.sumThirtySert} />
          <TablePart title='   14 ' colorClass='warning' data={props.sumFourteenSert} />
          <TablePart title=' ' colorClass='success' data={props.allTOCount} />
          <TablePart title=' ' colorClass='success' data={props.sertCount} />
          <TablePart title='  ( AlphaSMS)' colorClass='danger' data={props.balance} />

        </tbody>
      </table>
    </div>
  );
}

export default Main;
